import Chart from 'chart.js/auto'
import ChartDataSource from 'chartjs-plugin-datasource';

Chart.plugins.register(ChartDataSource);

// read the data from csv file
async function readData() {
  const data = await readAndParseCSV('../../data/dispenseTimes.csv');
  console.log(data);
  return data;
}


// render the chart

(async function() {
    const rawData = await readData();
    const data = parseData(rawData);
    console.log(data);

  
    new Chart(
      document.getElementById('dimensions'),
      {
        type: 'bubble',
        options: {
          plugins: {
            datasource: {
              url: 'sample-dataset.csv'
            }
          }
        },
        data: {
          labels: data.map(x => x.filled_datetime),
          datasets: [
            {
              label: 'Dimensions',
              data: data.map(row => ({
                x: row.filled_datetime,
                y: row.dispense_time,
                r: row.activity_error
              }))
            }
          ]
        }
      }
    );
  })();